body{
   margin: 0;
}

h1{
    display: none;
}

.container{
    width: 1280px;
    padding: 80px;
    margin: 0 auto;
}

.container__grey{
   width: 100%;
   background-color: var(--grey-more-light);
}
.container__blue{
    position: relative;
    width: 100%;
    padding: 0;
    padding-top: 80px;
    padding-bottom: 160px;
    background: linear-gradient(153deg, #12a1de 0%, #c6ffd9 100%);
}

.title{
    font-family: 'Tektur', sans-serif;
    font-weight: 600;
    font-size: 40px;
    line-height: 120%;
    text-transform: uppercase;
    text-align: center;
    color: var(--black);
    margin-bottom: 32px;
}
.subtitle{
    font-family: 'Fixel Display', sans-serif;
    font-weight: 400;
    font-size: 18px;
    line-height: 140%;
    text-align: center;
    color: var(--black);
    margin-bottom: 40px;
}
.text{
    font-family: 'Fixel Display', sans-serif;
    font-weight: 400;
    font-size: 16px;
    line-height: 140%; 
    color: var(--black);
}
.button{
    display: flex;
    border: none;
    border-radius: 32px;
    padding: 8px 8px 8px 24px;
    align-items: center;
    font-family: 'Fixel Display', sans-serif;
    font-weight: 700;
    font-size: 18px;
    line-height: 140%;
    color: var(--black);  
}
.button__image{
    margin-left: 12px;
}
.section__link{
    font-family: 'Fixel Display', sans-serif;
    font-weight: 500;
    font-size: 16px;
    line-height: 140%;
}